
/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/
::-webkit-scrollbar
{
  width: 8px;
  height: 16px;
  background-color: #767676;
}

/*定义滚动条轨道 内阴影+圆角*/
::-webkit-scrollbar-track
{
  background-color: #d4d4d4;
}

/*定义滑块 内阴影+圆角*/
::-webkit-scrollbar-thumb
{
  background-color: #737373;
}
body {
